/**
 * Data source for the Roles view. This class should
 * encapsulate all logic for fetching and manipulating the displayed data
 * (including sorting, pagination, and filtering).
 */
export class RolesDataSource extends DataSource<Role> {
  
  private rolesSubject = new BehaviorSubject<Role[]>([]);
  private loadingSubject = new BehaviorSubject<boolean>(false);
  public loading$ = this.loadingSubject.asObservable();
  data: Role[]=[];
  total_count: number;

  constructor(private userService: UserService) {
    super();
  }

  /**
   * Connect this data source to the table. The table will only update when
   * the returned stream emits new items.
   * @returns A stream of the items to be rendered.
   */
   
   loadRoles(filter:string,
			sortField:string,
			sortDirection:string,
			pageIndex:number,
			pageSize:number) {

	this.loadingSubject.next(true);

	this.userService.role_list(localStorage.getItem('token'), filter, sortField, sortDirection,
		pageIndex, pageSize).pipe(
			catchError(() => of([])),
			finalize(() => this.loadingSubject.next(false))
		)
		.subscribe((allroles) => {
			this.total_count = allroles.total_count,
			this.rolesSubject.next(allroles.roles)
		});
  }
   
  connect(collectionViewer: CollectionViewer): Observable<Role[]> {
	console.log("Connecting data source");
	return this.rolesSubject.asObservable();
  }

  disconnect(collectionViewer: CollectionViewer): void {
	this.rolesSubject.complete();
	this.loadingSubject.complete();
  }

  /**
   * Paginate the data (client-side). If you're using server-side pagination,
   * this would be replaced by requesting the appropriate data from the server.
   */

  /**
   * Sort the data (client-side). If you're using server-side sorting,
   * this would be replaced by requesting the appropriate data from the server.
   */

}

/** Simple sort comparator for example ID/Name columns (for client-side sorting). */
function compare(a, b, isAsc) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
