/**
 * Data source for the Permissions view. This class should
 * encapsulate all logic for fetching and manipulating the displayed data
 * (including sorting, pagination, and filtering).
 */
export class PermissionsDataSource extends DataSource<Privilege> {
  
  private privilegesSubject = new BehaviorSubject<Privilege[]>([]);
  private loadingSubject = new BehaviorSubject<boolean>(false);
  public loading$ = this.loadingSubject.asObservable();
  data: Privilege[]=[];
  total_count: number;

  constructor(private userService: UserService) {
    super();
  }

  /**
   * Connect this data source to the table. The table will only update when
   * the returned stream emits new items.
   * @returns A stream of the items to be rendered.
   */
   
   loadPrivileges(filter:string,
			sortField:string,
			sortDirection:string,
			pageIndex:number,
			pageSize:number) {

	this.loadingSubject.next(true);

	this.userService.privilege_list(localStorage.getItem('token'), filter, sortField, sortDirection,
		pageIndex, pageSize).pipe(
			catchError(() => of([])),
			finalize(() => this.loadingSubject.next(false))
		)
		.subscribe((allprivileges) => {
			this.total_count = allprivileges.total_count,
			this.privilegesSubject.next(allprivileges.privileges)
		});
  }
   
  connect(collectionViewer: CollectionViewer): Observable<Privilege[]> {
    // Combine everything that affects the rendered data into one update
    // stream for the data-table to consume.
    return this.privilegesSubject.asObservable();
  }

  /**
   *  Called when the table is being destroyed. Use this function, to clean up
   * any open connections or free any held resources that were set up during connect.
   */
  disconnect(collectionViewer: CollectionViewer): void {
	this.privilegesSubject.complete();
	this.loadingSubject.complete();
  }

}

/** Simple sort comparator for example ID/Name columns (for client-side sorting). */
function compare(a, b, isAsc) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
