import { Component } from '@angular/core';
import { FieldType, FormlyFieldConfig } from '@ngx-formly/core';

@Component({
  selector: 'formly-field-stepper',
  template: `
  <mat-vertical-stepper #stepper>
    <mat-step *ngFor="let step of field.fieldGroup; let index = index; let last = last;">
      <ng-template matStepLabel>{{ step.templateOptions.label }}</ng-template>
	  
		<formly-field [field]="step"></formly-field>

	  <p>
	  <div align="end">
		<div class="row">
		<div class="col">
		<button mat-button (click)="options.resetModel()">Reset</button>
		<button mat-button matStepperPrevious *ngIf="index !== 0" type="button">Back</button>
		<button mat-button matStepperNext *ngIf="!last" color="primary" type="button">Next</button>
		<button mat-button matStepperNext color="primary" type="button">Save</button>
		<button mat-raised-button matStepperNext *ngIf="last" color="warn" type="submit">Submit</button>        
      </div>
	  </div>
	  </div>
	  
    </mat-step>
  </mat-vertical-stepper>
`,
})
export class FormlyFieldStepper extends FieldType {
  isValid(field: FormlyFieldConfig) {
    if (field.key) {
      return field.formControl.valid;
    }

    return field.fieldGroup.every(f => this.isValid(f));
  }
}
