  private BASE_URL: string = environment.apiUrl;
  
  constructor(private http: HttpClient) {}
  
  login(user: User):Observable<any>{
    let url: string = `${this.BASE_URL}/login`;
    return this.http.post(url, user, httpOptions);
  }
  
